'use client';

import { useState, useEffect, useRef } from 'react';
import { useRouter, useParams } from 'next/navigation';
import { ChevronRight, Upload, X, Image as ImageIcon, ArrowLeft } from 'lucide-react';
import { api } from '@/app/_components/lib/api';
import EditorNew from '../../components/EditorNew';

interface OrganizationForm {
  id?: string;
  is_organization: boolean | number;
  name: string;
  logo: string | null;
  short_description: string;
  long_description: string;
  mentor_name: string;
  social_media_type: string;
  social_media_url: string;
  visi: string;
  misi: string;
  struktur: string;
  photo: string | null;
  photo_description: string;
  year: string;
  category: string;
}

export default function OrganizationFormPage() {
  const router = useRouter();
  const params = useParams();
  const organizationId = params?.id as string;
  const isEdit = organizationId !== 'create';

  // Refs for file inputs
  const logoInputRef = useRef<HTMLInputElement>(null);
  const photoInputRef = useRef<HTMLInputElement>(null);

  const [formData, setFormData] = useState<OrganizationForm>({
    is_organization: true,
    name: '',
    logo: null,
    short_description: '',
    long_description: '',
    mentor_name: '',
    social_media_type: 'instagram',
    social_media_url: '',
    visi: '',
    misi: '',
    struktur: '',
    photo: null,
    photo_description: '',
    year: '',
    category: '',
  });

  const [logoPreview, setLogoPreview] = useState<string | null>(null);
  const [photoPreview, setPhotoPreview] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [fetching, setFetching] = useState(false);

  // Organization categories
  const categories = [
    'OSIS', 'MPK', 'BDI', 'PALWAGA', 'PASKATEMA', 
    'TSBC', 'TSFC', 'TSVC', 'PMR', 'MEMO', 
    'MAC', 'METIC', 'COMET', 'PUSTEL', 'DA'
  ];

  const socialMediaTypes = [
    { value: 'instagram', label: 'Instagram' },
    { value: 'facebook', label: 'Facebook' },
    { value: 'twitter', label: 'Twitter' },
    { value: 'youtube', label: 'YouTube' },
    { value: 'tiktok', label: 'TikTok' },
  ];

  // Years
  const currentYear = new Date().getFullYear();
  const years = Array.from({ length: 5 }, (_, i) => {
    const year = currentYear - i;
    return `${year}/${year + 1}`;
  });

  // Fetch organization data if editing
  useEffect(() => {
    const fetchOrganization = async () => {
      if (!isEdit) return;

      try {
        setFetching(true);
        const response = await api.get(`/organization/${organizationId}`);
        const org = response.data.data;

        setFormData({
          id: org.id,
          is_organization: org.is_organization ?? true,
          name: org.name || '',
          logo: null,
          short_description: org.short_description || '',
          long_description: org.long_description || '',
          mentor_name: org.mentor?.name || '',
          social_media_type: org.social_media?.type || 'instagram',
          social_media_url: org.social_media?.url || '',
          visi: org.visi || '',
          misi: org.misi || '',
        });

        if (org.logo) setLogoPreview(org.logo);
        if (org.photo) setPhotoPreview(org.photo);
      } catch (err) {
        console.error('Error fetching organization:', err);
        alert('Gagal memuat data organisasi');
        router.push('/admin/organisasi');
      } finally {
        setFetching(false);
      }
    };

    fetchOrganization();
  }, [isEdit, organizationId, router]);

  const handleInputChange = (field: string, value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const uploadToCloudinary = async (file: File): Promise<string> => {
    const cloudName = process.env.NEXT_PUBLIC_CLOUDINARY_CLOUD_NAME;
    const uploadPreset = process.env.NEXT_PUBLIC_CLOUDINARY_UPLOAD_PRESET;
    
    if (!cloudName || !uploadPreset) {
      throw new Error('Cloudinary not configured');
    }

    const formData = new FormData();
    formData.append('file', file);
    formData.append('upload_preset', uploadPreset);

    const response = await fetch(
      `https://api.cloudinary.com/v1_1/${cloudName}/image/upload`,
      { method: 'POST', body: formData }
    );

    if (!response.ok) {
      throw new Error('Upload failed');
    }

    const data = await response.json();
    return data.secure_url;
  };

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>, type: 'logo' | 'photo') => {
    const file = e.target.files?.[0];
    
    if (!file) return;

    // Validate file type
    if (!file.type.startsWith('image/')) {
      alert('Please select an image file!');
      return;
    }

    // Validate file size (max 5MB)
    if (file.size > 5 * 1024 * 1024) {
      alert('File size must be less than 5MB!');
      return;
    }

    const reader = new FileReader();
    
    reader.onload = () => {
      const result = reader.result as string;
      
      if (type === 'logo') {
        setLogoPreview(result);
        setFormData(prev => ({ ...prev, logo: file as any }));
      } else {
        setPhotoPreview(result);
        setFormData(prev => ({ ...prev, photo: file as any }));
      }
    };

    reader.onerror = () => {
      alert(`Failed to read ${type}. Please try again.`);
    };

    reader.readAsDataURL(file);
    e.target.value = '';
  };

  const removeFile = (type: 'logo' | 'photo') => {
    if (type === 'logo') {
      setLogoPreview(null);
      setFormData(prev => ({ ...prev, logo: null }));
      if (logoInputRef.current) logoInputRef.current.value = '';
    } else {
      setPhotoPreview(null);
      setFormData(prev => ({ ...prev, photo: null }));
      if (photoInputRef.current) photoInputRef.current.value = '';
    }
  };

  const handleSubmit = async () => {
    // Validation
    if (!formData.name || !formData.category || !formData.year) {
      alert('Nama, Kategori, dan Tahun harus diisi!');
      return;
    }

    setLoading(true);
    try {
      let logoUrl = logoPreview;
      let photoUrl = photoPreview;

      // Upload logo if it's a new file
      if (formData.logo && typeof formData.logo !== 'string') {
        logoUrl = await uploadToCloudinary(formData.logo as any);
      }

      // Upload photo if it's a new file
      if (formData.photo && typeof formData.photo !== 'string') {
        photoUrl = await uploadToCloudinary(formData.photo as any);
      }

      const payload = {
        is_organization: formData.is_organization,
        name: formData.name,
        logo: logoUrl,
        short_description: formData.short_description,
        long_description: formData.long_description,
        visi: formData.visi,
        misi: formData.misi,
      };

      if (isEdit) {
        await api.put(`/organizations/${organizationId}`, JSON.stringify(payload));
        alert('Organisasi berhasil diupdate!');
      } else {
        await api.post('/organizations', JSON.stringify(payload));
        alert('Organisasi berhasil dibuat!');
      }

      router.push('/admin/organisasi');
    } catch (err) {
      console.error('Error saving organization:', err);
      alert('Gagal menyimpan organisasi');
    } finally {
      setLoading(false);
    }
  };

  if (fetching) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <p className="text-gray-600">Loading organization data...</p>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Breadcrumb */}
      <div className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-6 py-4">
          <div className="flex items-center space-x-2 text-sm text-gray-600">
            <span>Admin</span>
            <ChevronRight className="w-4 h-4" />
            <button onClick={() => router.push('/admin/organisasi')} className="hover:text-red-500">
              Organisasi
            </button>
            <ChevronRight className="w-4 h-4" />
            <span className="text-gray-900">{isEdit ? 'Edit' : 'Create'} Organization</span>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-6 py-8">
        <div className="mb-8">
          <button
            onClick={() => router.back()}
            className="flex items-center space-x-2 mb-4 text-gray-600 hover:text-gray-900"
          >
            <ArrowLeft className="w-5 h-5" />
            <span>Kembali</span>
          </button>
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            {isEdit ? 'Edit' : 'Create'} Organization
          </h1>
          <p className="text-gray-600">Introduce your organization's profile to the world!</p>
        </div>

        <div className="bg-white rounded-lg shadow-sm p-8">
          {/* Type Selection */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Tipe<span className="text-red-500">*</span>
            </label>
            <div className="flex gap-4">
              <label className="flex items-center">
                <input
                  type="radio"
                  checked={formData.is_organization === 1}
                  onChange={() => handleInputChange('is_organization', 1)}
                  className="mr-2"
                />
                Organisasi
              </label>
              <label className="flex items-center">
                <input
                  type="radio"
                  checked={formData.is_organization === 0}
                  onChange={() => handleInputChange('is_organization', 0)}
                  className="mr-2"
                />
                Sub-Organisasi
              </label>
            </div>
          </div>

          {/* Category & Year */}
          <div className="grid grid-cols-2 gap-4 mb-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Kategori<span className="text-red-500">*</span>
              </label>
              <select
                value={formData.category}
                onChange={(e) => handleInputChange('category', e.target.value)}
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
              >
                <option value="">Pilih Kategori</option>
                {categories.map(cat => (
                  <option key={cat} value={cat}>{cat}</option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Tahun<span className="text-red-500">*</span>
              </label>
              <select
                value={formData.year}
                onChange={(e) => handleInputChange('year', e.target.value)}
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
              >
                <option value="">Pilih Tahun</option>
                {years.map(year => (
                  <option key={year} value={year}>{year}</option>
                ))}
              </select>
            </div>
          </div>

          {/* Name */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Nama Organisasi<span className="text-red-500">*</span>
            </label>
            <input
              type="text"
              value={formData.name}
              onChange={(e) => handleInputChange('name', e.target.value)}
              placeholder="Contoh: OSIS SMKN 1 Cimahi"
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
            />
          </div>

          {/* Logo Upload */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Logo<span className="text-red-500">*</span>
            </label>
            {logoPreview ? (
              <div className="relative w-32 h-32 bg-gray-100 rounded-lg overflow-hidden border-2 border-gray-300">
                <img src={logoPreview} alt="Logo preview" className="w-full h-full object-cover" />
                <button
                  type="button"
                  onClick={() => removeFile('logo')}
                  className="absolute top-2 right-2 p-1 bg-red-500 text-white rounded-full hover:bg-red-600 transition-colors"
                >
                  <X className="w-4 h-4" />
                </button>
              </div>
            ) : (
              <button
                type="button"
                onClick={() => logoInputRef.current?.click()}
                className="w-32 h-32 bg-gray-100 rounded-lg flex flex-col items-center justify-center border-2 border-dashed border-gray-300 hover:border-red-500 hover:bg-red-50 transition-colors cursor-pointer"
              >
                <Upload className="w-8 h-8 text-gray-400 mb-2" />
                <span className="text-xs text-gray-500">Click to upload</span>
              </button>
            )}
            <input
              ref={logoInputRef}
              type="file"
              accept="image/*"
              onChange={(e) => handleFileChange(e, 'logo')}
              className="hidden"
            />
            <p className="mt-2 text-xs text-gray-500">PNG, JPG up to 5MB</p>
          </div>

          {/* Short Description */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Deskripsi Singkat<span className="text-red-500">*</span>
            </label>
            <textarea
              value={formData.short_description}
              onChange={(e) => handleInputChange('short_description', e.target.value)}
              placeholder="Deskripsi singkat organisasi (maksimal 200 karakter)"
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none resize-none"
              rows={3}
              maxLength={200}
            />
            <p className="mt-1 text-xs text-gray-500">
              {formData.short_description.length}/200 karakter
            </p>
          </div>

          {/* Long Description with EditorNew */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Deskripsi Lengkap
            </label>
            <EditorNew 
              value={formData.long_description}
              onChange={(content) => handleInputChange('long_description', content)}
            />
          </div>

          {/* Photo Upload */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Foto
            </label>
            {photoPreview ? (
              <div className="relative w-full h-48 bg-gray-100 rounded-lg overflow-hidden border-2 border-gray-300">
                <img src={photoPreview} alt="Photo preview" className="w-full h-full object-cover" />
                <button
                  type="button"
                  onClick={() => removeFile('photo')}
                  className="absolute top-2 right-2 p-1 bg-red-500 text-white rounded-full hover:bg-red-600 transition-colors"
                >
                  <X className="w-4 h-4" />
                </button>
              </div>
            ) : (
              <button
                type="button"
                onClick={() => photoInputRef.current?.click()}
                className="w-full h-48 bg-gray-100 rounded-lg flex flex-col items-center justify-center border-2 border-dashed border-gray-300 hover:border-red-500 hover:bg-red-50 transition-colors cursor-pointer"
              >
                <ImageIcon className="w-12 h-12 text-gray-400 mb-2" />
                <span className="text-sm text-gray-500">Click to upload photo</span>
              </button>
            )}
            <input
              ref={photoInputRef}
              type="file"
              accept="image/*"
              onChange={(e) => handleFileChange(e, 'photo')}
              className="hidden"
            />
            <p className="mt-2 text-xs text-gray-500">PNG, JPG up to 5MB</p>
          </div>

          {/* Photo Description */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Deskripsi Foto
            </label>
            <input
              type="text"
              value={formData.photo_description}
              onChange={(e) => handleInputChange('photo_description', e.target.value)}
              placeholder="Foto anggota/Foto Kegiatan/Lain-lain"
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
            />
          </div>

          {/* Mentor */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Pembimbing
            </label>
            <input
              type="text"
              value={formData.mentor_name}
              onChange={(e) => handleInputChange('mentor_name', e.target.value)}
              placeholder="Nama Pembimbing"
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
            />
          </div>

          {/* Social Media */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Social Media
            </label>
            <div className="grid grid-cols-3 gap-4">
              <select
                value={formData.social_media_type}
                onChange={(e) => handleInputChange('social_media_type', e.target.value)}
                className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
              >
                {socialMediaTypes.map(type => (
                  <option key={type.value} value={type.value}>{type.label}</option>
                ))}
              </select>
              <input
                type="url"
                value={formData.social_media_url}
                onChange={(e) => handleInputChange('social_media_url', e.target.value)}
                placeholder="https://instagram.com/..."
                className="col-span-2 px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none"
              />
            </div>
          </div>

          {/* Visi with EditorNew */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Visi
            </label>
            <EditorNew 
              value={formData.visi}
              onChange={(content) => handleInputChange('visi', content)}
            />
          </div>

          {/* Misi with EditorNew */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Misi
            </label>
            <EditorNew 
              value={formData.misi}
              onChange={(content) => handleInputChange('misi', content)}
            />
          </div>

          {/* Struktur */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Struktur Organisasi
            </label>
            <textarea
              value={formData.struktur}
              onChange={(e) => handleInputChange('struktur', e.target.value)}
              placeholder="Struktur organisasi"
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500 focus:border-transparent outline-none resize-none"
              rows={6}
            />
          </div>

          {/* Action Buttons */}
          <div className="flex justify-end gap-4 pt-6 border-t">
            <button
              type="button"
              onClick={() => router.back()}
              disabled={loading}
              className="px-6 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors disabled:opacity-50"
            >
              Cancel
            </button>
            <button
              type="button"
              onClick={handleSubmit}
              disabled={loading}
              className="px-6 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors disabled:opacity-50"
            >
              {loading ? 'Menyimpan...' : isEdit ? 'Update Organization' : 'Create Organization'}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}